import React, { useEffect, useState } from 'react';
import { Button, Input, message, Modal, Table } from 'antd';
import type { ColumnsType } from 'antd/es/table';
import { DeleteOutlined, ExclamationCircleOutlined } from '@ant-design/icons';
import { connect } from 'react-redux';
import { Dispatch, bindActionCreators } from 'redux';
import * as BlogActions from '@/redux/actionCreator';
import MyPagination from '@/components/pagination';
import dayjs from 'dayjs';
import jwtDecode from 'jwt-decode';
import { handleNotDelete } from '@/utils/prompt';
const { confirm } = Modal;
const { Search } = Input;

interface DataType {
  targetReplayContent: string;
  currentReplayContent: string;
  children: DataType[] | string;
  key?: React.Key;
  _id: string;
  username?: string;
  avatar?: string;
  articleIds?: string[];
  createTime: string;
  updateTime: string;
}
interface CommentData {
  totalCount: number;
  page: number;
  pageSize: number;
  data: DataType[];
}
const Comments = (props: any) => {
  const token = jwtDecode(localStorage.getItem('token') as string) as object | any;
  const role_type = token[0].role[0].role_type
  const columns: ColumnsType<DataType> = [
    {
      title: 'æ–‡ç« æ ‡é¢˜',
      dataIndex: 'articleTitle',
      fixed: 'left',
      width: '20rem',
    },
    {
      title: 'æ˜µç§°',
      dataIndex: 'nickName',
      width: 150,
    },
    {
      title: 'å½“å‰å›å¤å†…å®¹',
      dataIndex: 'currentReplayContent',
      render: (_, record) => {
        return <p className='introduction' style={{ width: '10rem' }}>{record.currentReplayContent}</p>;
      },
    },
    {
      title: 'ç›®æ ‡å›å¤ID',
      dataIndex: 'targetReplayId',
      width: '10rem'
    },
    {
      title: 'ç›®æ ‡å›å¤å†…å®¹',
      dataIndex: 'targetReplayContent',
      render: (_, record) => {
        return <p className='introduction' style={{ width: '10rem' }}>{record.targetReplayContent}</p>;
      },
    },

    {
      title: 'è¯„è®ºæ—¶é—´',
      dataIndex: 'commentTime',
      width: '16rem',
      render: time => {
        return dayjs(time * 1000).format('YYYY-MM-DD HH:mm:ss');
      },
    },
    {
      title: 'æ“ä½œ',
      key: 'action',
      className: 'handle_btn',
      width: 120,
      render: item => {
        return (
          <div>
            <Button
              type="primary"
              danger
              shape="circle"
              // disabled={role_type}
              icon={<DeleteOutlined />}
              onClick={() => {
                commentDelete(item);
              }}
              style={{ marginRight: '5px' }}
            />
          </div>
        );
      },
    },
  ];

  // è¯„è®ºåˆ—è¡¨
  const [list, setList] = useState<DataType[]>([]);
  // åˆ†é¡µæ€»æ•°
  const [total, setTotal] = useState(0);
  // å½“å‰ç¬¬å‡ é¡µ
  const [currentPage, setCurrentPage] = useState(1);
  // æ¯é¡µæ˜¾ç¤ºæ¡æ•°
  const [pageSize, setPageSize] = useState(15);
  // è·å–è¯„è®ºåˆ—è¡¨æ•°æ®
  useEffect(() => {
    props.BlogActions.asyncCommentsAction(currentPage, pageSize, '').then((res: CommentData) => {
      // è·å–è¯„è®º
      const { data, totalCount, page, pageSize } = res.data as unknown as CommentData;
      // æ— å­èŠ‚ç‚¹æƒ…å†µä¸‹ï¼ŒæŒ‰é’®
      data.forEach((item) => {
        if (item.children.length === 0) {
          item.children = '';
        }
      });
      setList(data);
      setTotal(totalCount);
      setCurrentPage(page);
      setPageSize(pageSize);
    });
  }, [currentPage, pageSize, props.BlogActions]);
  // åˆ é™¤è¯„è®º
  const commentDelete = (item: DataType) => {
    confirm({
      title: 'ä½ ç¡®å®šè¦åˆ é™¤å—?',
      icon: <ExclamationCircleOutlined />,
      onOk() {
        if (role_type) {
          return handleNotDelete();
        }
        // å…ˆå°†è¦åˆ é™¤çš„æ•°æ®è¿‡æ»¤æ‰å†è°ƒç”¨æ¥å£
        setList(list.filter((it: { _id: string }) => it._id !== item._id));
        message.success('è¯„è®ºåˆ é™¤æˆåŠŸ');
        props.BlogActions.asyncCommentDeleteAction(item._id).then(() => {
          props.BlogActions.asyncCommentsAction(currentPage, pageSize, '').then((res: CommentData) => {
            // è·å–è¯„è®º
            const { data, totalCount, page, pageSize } = res.data as unknown as CommentData;
            setList(data);
            setTotal(totalCount);
            setCurrentPage(page);
            setPageSize(pageSize);
          });
        });
      },
    });
  };
  // æœç´¢
  const onSearch = (articleTitle: string) => {
    props.BlogActions.asyncCommentsAction(currentPage, pageSize, articleTitle).then((res: CommentData) => {
      const { data, totalCount, page, pageSize } = res.data as unknown as CommentData;
      setList(data);
      setTotal(totalCount);
      setCurrentPage(page);
      setPageSize(pageSize);
    });
  };

  // è·³è½¬é¡µæ•°æ®æ˜¾ç¤º
  const onChangePage = (page: number, pageSize: number, params = '') => {
    // é‡æ–°è°ƒç”¨æ¥å£å°†å‚æ•°ä¼ é€’è¿‡å»
    props.BlogActions.asyncCommentsAction(page, pageSize, params).then((res: CommentData) => {
      // è·å–åˆ—è¡¨æ•°æ®
      let { data } = res.data as unknown as CommentData;
      setList(data);
      // åˆ‡æ¢è¡Œ
      setCurrentPage(page);
      // æ ¹æ®é¡µé¢æ•°æ®æ˜¾ç¤ºé¡µç 
      setPageSize(pageSize);
    });
  };
  return (
    <div>
      <div className="title">
        <div></div>
        <div className="search_box">
          <Search
            className="search"
            allowClear
            placeholder="è¯·è¾“å…¥æ–‡ç« åç§°"
            onSearch={onSearch}
            enterButton
          />
        </div>
      </div>
      <Table
        columns={columns}
        dataSource={list}
        scroll={{ y: 'calc(100vh - 240px)', x: true }}
        rowKey={(item: DataType) => {
          return item._id;
        }}
        pagination={false}
      />
      <MyPagination
        pageSize={pageSize}
        currentPage={currentPage}
        total={total}
        onChange={onChangePage}
      ></MyPagination>
    </div>
  );
};

const mapDispatchToProps = (dispatch: Dispatch) => {
  return {
    BlogActions: bindActionCreators(BlogActions, dispatch),
  };
};
export default connect(null, mapDispatchToProps)(Comments);
